import React, { Component } from 'react';
import { List, Grid, Table, Label, Segment, Button, Menu, Image, Icon, Header, Accordion, Radio, Form, Divider, Dropdown } from 'semantic-ui-react'

class DataPanel extends Component {

    state = {
        _site: '',
        Location: 'x',
        Parameter: 'x',
        MDL: 'x',
        Sampled: 'x',
        Detected: 'x',
        ExceededMDL: 'x',
        Min: 'x',
        Max: 'x',
        Units: 'x',
        siteTableData: [],
        siteParamsKeys: []
    }


    setData = (data) => {
        console.log(data)
        this.setState({data:data})
        var sites = [];
        for( var [key] of data) {
            sites.push(key)
        }
        this.setState({siteTableData: sites})
    }

    setParamKeys = (site) => {
        var dd = []
        var mm = this.state.data.get(site)
        for( var [key] of mm ){
            dd.push(key)
        }
        this.setState({siteParamsKeys: dd})
    }

    handelSiteListClicked = (e, {content}) => {
        this.setState({_site: content})
        this.setParamKeys(content)
    }

    handleParamsKeyListClicked = (e, {content}) => {
        var param = this.state.data.get(this.state._site).get(content)
        this.setState({Location: param['Location']})
        this.setState({Parameter: param['Parameter']})
        this.setState({MDL: param['MDL']})
        this.setState({Sampled: param['Sampled']})
        this.setState({Detected: param['Detected']})
        this.setState({ExceededMDL: param['Exceeded MDL']})
        this.setState({Min: param['Min']})
        this.setState({Max: param['Max']})
        this.setState({Units: param['Unit']})
        console.log(param)
    }
    handleParamKeysTableClick = (e,obj) => {
        console.log(obj);
    }

    handleRowClick = (obj) => {
        var text = obj.target
        console.log(text.innerText);
    }
    render() {
        return (
            <div style={{ textAlign: 'left' }}>
                <Segment raised>
                    <Table celled>
                        <Table.Body>
                            <Table.Row>
                                <Table.Cell>Location</Table.Cell>
                                <Table.Cell>{this.state.Location}</Table.Cell>
                            </Table.Row>
                            <Table.Row>
                                <Table.Cell>Parameter</Table.Cell>
                                <Table.Cell>{this.state.Parameter}</Table.Cell>
                            </Table.Row>
                            <Table.Row>
                                <Table.Cell>MDL</Table.Cell>
                                <Table.Cell>{this.state.MDL || 'x'}</Table.Cell>
                            </Table.Row>
                            <Table.Row>
                                <Table.Cell>Sampled</Table.Cell>
                                <Table.Cell>{this.state.Sampled}</Table.Cell>
                            </Table.Row>
                            <Table.Row>
                                <Table.Cell>Detected</Table.Cell>
                                <Table.Cell>{this.state.Detected}</Table.Cell>
                            </Table.Row>
                            <Table.Row>
                                <Table.Cell>Exceeded MDL</Table.Cell>
                                <Table.Cell>{this.state.ExceededMDL}</Table.Cell>
                            </Table.Row>
                            <Table.Row>
                                <Table.Cell>Min Value</Table.Cell>
                                <Table.Cell>{this.state.Min}</Table.Cell>
                            </Table.Row>
                            <Table.Row>
                                <Table.Cell>Max Value</Table.Cell>
                                <Table.Cell>{this.state.Max}</Table.Cell>
                            </Table.Row>
                            <Table.Row>
                                <Table.Cell>Units</Table.Cell>
                                <Table.Cell>{this.state.Units}</Table.Cell>
                            </Table.Row>
                        </Table.Body>
                    </Table>
                    {/* <Table
                        celled
                        renderBodyRow={this.renderParamBodyRow}
                        tableData={this.state._param}
                    /> */}
                    <Button attached='left'>Reset</Button>
                    <Button attached='right'>Clear</Button>
                    <Divider />
                    <Grid>
                        <Grid.Column width={4} >
                            {/* <Table
                                celled
                                renderBodyRow={this.renderSiteBodyRow}
                                tableData={this.state.siteTableData}
                            /> */}
                            <List link={true} relaxed items={this.state.siteTableData} onItemClick={this.handelSiteListClicked} />
                        </Grid.Column>
                        <Grid.Column width={12} >
                            {/* <Table 
                                celled
                                renderBodyRow={this.renderParameterBodyRow}
                                tableData={this.state.siteParamsKeys}
                                onClick={this.handleParamKeysTableClick}
                            /> */}
                            <List divided relaxed items={this.state.siteParamsKeys} onItemClick={this.handleParamsKeyListClicked}/>
                        </Grid.Column>
                    </Grid>
                </Segment>
            </div>
        )
    }
}

export default DataPanel;


