import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import { Dimmer, Loader, Container, Sidebar, Segment, Button, Menu, Image, Icon, Header, Accordion } from 'semantic-ui-react'
import SimpleMap from './SimpleMap'
import CtrlPanel from './CtrlPanel'
import DataPanel from './DataPanel'

class App extends Component {
  state = {
    ctrlPanelVisible: false,
    dataPanelvisible: false,
    ctrlAccordionIndex: 0,
    loaderOn: false
  }

  handleAccordionClick = (e, titleProps) => {
    const { index } = titleProps
    const { ctrlAccordionIndex } = this.state
    const newIndex = ctrlAccordionIndex === index ? -1 : index

    this.setState({ ctrlAcoordionIndex: newIndex })
  }

  toggleVisibility = () => this.setState({ ctrlPanelVisible: !this.state.ctrlPanelVisible })
  handleItemClick = (e, { name }) => {
    this.setState({ activeItem: name })
    if (name === 'Ctrl Panel') {
      this.setState({ ctrlPanelVisible: !this.state.ctrlPanelVisible })
    }

    if (name === 'Data Panel') {
      this.setState({ dataPanelvisible: !this.state.dataPanelvisible })
    }
  }

  onTimeQuerySubmit = (data) => {
    console.log("onTimeQuerySubmit:");
    console.log(data);
    this.setState({loaderOn: true})

    fetch('http://54.213.115.18/reachwcfrest/EAGLE_GWDP.svc/queryPeriodBasedMapPOST/',{
      method: 'POST',
      body: '<args><WellType>up</WellType><DetectionType>detections</DetectionType><PeriodType>all</PeriodType><PeriodString></PeriodString><ParamGroup>all</ParamGroup><Param>all</Param></args>'
    })
    .then((response) => response.json())
    .then((responseJson) => {
      this.setState({loaderOn: false})
      let res = JSON.parse(responseJson['GetPeriodBasedMapQueryResultPostResult'])
      var dataMap = new Map()
      for( var ele of res ){
        if( dataMap.has(ele['Location']) === false ){
          var paramMap = new Map()
          paramMap.set(ele['Parameter'], ele);
          dataMap.set(ele['Location'], paramMap)
        }
        else {
          var m = dataMap.get(ele['Location']);
          m.set(ele['Parameter'], ele)
          dataMap.set(ele['Location'], m)
        }   
      }
      this.dataPanel.setData(dataMap);
    })
    .catch((error) => {
      this.setState({loaderOn: false})
      console.error(error);
    });
  }

  render() {
    const { ctrlPanelVisible } = this.state
    const { dataPanelvisible } = this.state
    const { activeItem } = this.state
    const { ctrlAccordionIndex } = this.state
    const { loaderOn } = this.state

    return (
      <div style={{ height: '100%' }}>
        {/* <Button onClick={this.toggleVisibility}>Toggle Visibility</Button> */}
        <Menu>
          <Menu.Item header>Eagle Mine</Menu.Item>
          <Menu.Item
            name='Ctrl Panel'
            active={ctrlPanelVisible}
            onClick={this.handleItemClick}
          >
            Ctrl Panel
          </Menu.Item>
          <Menu.Item
            name='Data Panel'
            active={dataPanelvisible}
            onClick={this.handleItemClick}
          >
            Data Panel
          </Menu.Item>
        </Menu>
        <Sidebar.Pushable as={Segment}>
          <Dimmer active={loaderOn}>
            <Loader>Loading</Loader>
          </Dimmer>
          <Sidebar as={Menu} animation='push' width='wide' visible={ctrlPanelVisible} icon='labeled' vertical >
            <CtrlPanel onTimeQuerySubmit={this.onTimeQuerySubmit}/>
          </Sidebar>
          <Sidebar as={Menu} animation='push' width='wide' direction='right' visible={dataPanelvisible} icon='labeled' vertical >
            <DataPanel ref={panel => { this.dataPanel = panel; }} />
          </Sidebar>
          <Sidebar.Pusher>
            <SimpleMap google={this.props.google}/>
          </Sidebar.Pusher>
        </Sidebar.Pushable>

      </div>
    )
  }
}

export default App;



