import React, { Component } from 'react';
import { Segment, Button, Menu, Image, Icon, Header, Accordion, Radio, Form, Divider, Dropdown } from 'semantic-ui-react'

class CtrlPanel extends Component {
    state = {
        activeIndex: 0,
        wells: 'all',
        detectionType: 'Detections',
        period: 'Full',
        explorerParamGroups: 'All',
        explorerParam: 'All'
    }
    componentWillMount() {
        console.log('componentWillMount');
    }

    handleClick = (e, titleProps) => {
        const { index } = titleProps
        const { activeIndex } = this.state
        const newIndex = activeIndex === index ? -1 : index

        this.setState({ activeIndex: newIndex })
    }

    handleWellsChange = (e, { value }) => this.setState({ wells: value })
    handelDetectionTypeChange = (e, { value }) => this.setState({ detectionType: value })
    handelPeriodChange = (e, { value }) => this.setState({ period: value })
    handleExplorerParamGroupsChange = (e, obj) => {
        console.log(obj)
        this.setState({ explorerParamGroups: obj['value'] });
    }

    handleExplorerParamChange = (e, { value }) => {
        this.setState({ explorerParam: value });
    }

    handleTimeQuerySubmit = () => {        
        this.props.onTimeQuerySubmit(this.state)
    }

    render() {
        const { activeIndex } = this.state
        const explorerParamGroupOptions = [
            { text: 'All', value: 'All' },
            { text: 'Field', value: 'Field' },
            { text: 'Metals', value: 'Metals' },
            { text: 'Major Anions', value: 'Major Anions' },
            { text: 'Major Cations', value: 'Major Cations' },
            { text: 'General', value: 'General' }
        ]

        const explorerParamOptions = [
            { text: 'All', value: 'All' },
            { text: 'Aluminum', value: 'Aluminum' },
            { text: 'Antimony', value: 'Antimony' },
        ]


        return (
            <div style={{ textAlign: 'left' }}>
                <Accordion>
                    <Accordion.Title active={activeIndex === 0} index={0} onClick={this.handleClick}>
                        <Icon name='dropdown' />
                        Time period based query
                    </Accordion.Title>
                    <Accordion.Content active={activeIndex === 0}>
                        <Segment raised>
                            <Form>
                                <Form.Field>
                                    Choose Wells: <b>{this.state.wells}</b>
                                </Form.Field>
                                <Form.Field>
                                    <Radio
                                        label='All Wells'
                                        name='radioGroup'
                                        value='all'
                                        checked={this.state.wells === 'all'}
                                        onChange={this.handleWellsChange}
                                    />
                                </Form.Field>
                                <Form.Field>
                                    <Radio
                                        label='Upgradient Wells'
                                        name='radioGroup'
                                        value='up'
                                        checked={this.state.wells === 'up'}
                                        onChange={this.handleWellsChange}
                                    />
                                </Form.Field>
                                <Form.Field>
                                    <Radio
                                        label='Downgradient Wells'
                                        name='radioGroup'
                                        value='down'
                                        checked={this.state.wells === 'down'}
                                        onChange={this.handleWellsChange}
                                    />
                                </Form.Field>
                            </Form>
                            <Divider />
                            <Form>
                                <Form.Field>
                                    Detection Type: <b>{this.state.detectionType}</b>
                                </Form.Field>
                                <Form.Field>
                                    <Radio
                                        label='Detections'
                                        name='radioGroup'
                                        value='Detections'
                                        checked={this.state.detectionType === 'Detections'}
                                        onChange={this.handelDetectionTypeChange}
                                    />
                                </Form.Field>
                                <Form.Field>
                                    <Radio
                                        label='Exceed MDL'
                                        name='radioGroup'
                                        value='Exceed'
                                        checked={this.state.detectionType === 'Exceed'}
                                        onChange={this.handelDetectionTypeChange}
                                    />
                                </Form.Field>
                            </Form>
                            <Divider />
                            <Form>
                                <Form.Field>
                                    For the period of: <b>{this.state.period}</b>
                                </Form.Field>
                                <Form.Field>
                                    <Radio
                                        label='Full Record'
                                        name='radioGroup'
                                        value='Full'
                                        checked={this.state.period === 'Full'}
                                        onChange={this.handelPeriodChange}
                                    />
                                </Form.Field>
                                <Form.Field>
                                    <Radio
                                        label='Baseline'
                                        name='radioGroup'
                                        value='Baseline'
                                        checked={this.state.period === 'Baseline'}
                                        onChange={this.handelPeriodChange}
                                    />
                                </Form.Field>
                                <Form.Field>
                                    <Radio
                                        label='Operational'
                                        name='radioGroup'
                                        value='Operational'
                                        checked={this.state.period === 'Operational'}
                                        onChange={this.handelPeriodChange}
                                    />
                                </Form.Field>
                                <Form.Field>
                                    <Radio
                                        label='Custom Period'
                                        name='radioGroup'
                                        value='Custom'
                                        checked={this.state.period === 'Custom'}
                                        onChange={this.handelPeriodChange}
                                    />
                                </Form.Field>
                                <Dropdown text='File' style={{ visibility: (this.state.period === 'Custom') ? 'visible' : 'hidden' }} >
                                    <Dropdown.Menu>
                                        <Dropdown.Item text='New' />
                                        <Dropdown.Item text='Open...' description='ctrl + o' />
                                        <Dropdown.Item text='Save as...' description='ctrl + s' />
                                        <Dropdown.Item text='Rename' description='ctrl + r' />
                                        <Dropdown.Item text='Make a copy' />
                                        <Dropdown.Item icon='folder' text='Move to folder' />
                                        <Dropdown.Item icon='trash' text='Move to trash' />
                                        <Dropdown.Divider />
                                        <Dropdown.Item text='Download As...' />
                                        <Dropdown.Item text='Publish To Web' />
                                        <Dropdown.Item text='E-mail Collaborators' />
                                    </Dropdown.Menu>
                                </Dropdown>
                            </Form>
                            <Divider />
                            <Form>
                                <Form.Field>
                                    Explore parameters groups (optional): <b>{this.state.explorerParamGroups}</b>
                                </Form.Field>
                                <Dropdown text={this.state.explorerParamGroups} options={explorerParamGroupOptions} simple onChange={this.handleExplorerParamGroupsChange} />
                            </Form>
                            <Divider />
                            <Form>
                                <Form.Field>
                                    Explore parameters (optional): <b>{this.state.explorerParam}</b>
                                </Form.Field>
                                <Dropdown text={this.state.explorerParam} options={explorerParamOptions} simple onChange={this.handleExplorerParamChange} />
                            </Form>
                            <Divider />
                            <Button primary fluid onClick={this.handleTimeQuerySubmit}>Submit</Button>
                        </Segment>
                    </Accordion.Content>

                    <Accordion.Title active={activeIndex === 1} index={1} onClick={this.handleClick}>
                        <Icon name='dropdown' />
                        Zone based query
                    </Accordion.Title>
                    <Accordion.Content active={activeIndex === 1}>
                        <p>
                            There are many breeds of dogs. Each breed varies in size and temperament. Owners often select a breed of
                        {' '}dog that they find to be compatible with their own lifestyle and desires from a companion.
                    </p>
                    </Accordion.Content>

                    <Accordion.Title active={activeIndex === 2} index={2} onClick={this.handleClick}>
                        <Icon name='dropdown' />
                        Table of Contents
                    </Accordion.Title>
                    <Accordion.Content active={activeIndex === 2}>
                        <p>
                            Three common ways for a prospective owner to acquire a dog is from pet shops, private owners, or shelters.
                    </p>
                        <p>
                            A pet shop may be the most convenient way to buy a dog. Buying a dog from a private owner allows you to
                        {' '}assess the pedigree and upbringing of your dog before choosing to take it home. Lastly, finding your
                        {' '}dog from a shelter, helps give a good home to a dog who may not find one so readily.
                    </p>
                    </Accordion.Content>
                </Accordion>
            </div>
        )
    }
}

export default CtrlPanel;


