import React from 'react';
import ReactDOM from 'react-dom'
import { Map, InfoWindow, Marker, Polygon, GoogleApiWrapper } from 'google-maps-react';
var downStations = require('./data/gwdp_background_stations.json')
var upStations = require('./data/gwdp_compliance_stations.json')
var fence = require('./data/facility_fence.json')
var featureCollectionCoords = require('./data/ore_body_and_outcrop.json')



export class SimpleMap extends React.Component {
  constructor(props) {
    super(props);
    console.log(props)
    this.state = {
      showingInfoWindow: false,
      activeMarker: {},
      selectedPlace: {},
      center: {
        lat: 46.7487177, lng: -87.884195
      }
    }
  }

  onMarkerClick = (props, marker, e) => {
    console.log('this is:', props);
    this.setState({
      selectedPlace: props,
      activeMarker: marker,
      showingInfoWindow: true
    });
  }

  onMapReady = () => {
    console.log('map ready')

  }

  onMapClicked = () => {
    console.log('this is:', this);
    if (this.state.showingInfoWindow) {
      this.setState({
        showingInfoWindow: false,
        activeMarker: null
      })
    }
  }

  onChangeMapCenter = (point) => {
    this.props.changeMapCenter(point);
  }

  makeMarker = (name, coord, textColor, strokeColor) => {

    const { google } = this.props
    return (
      <Marker position={coord}
        label={{
          text: name,
          color: textColor,
          fontSize: '16px',
          fontWeight: 'bold'
        }}
        icon={{
          path: (google && google.maps.SymbolPath.CIRCLE),
          strokeColor: strokeColor,
          scale: 5,
          labelOrigin: (google && new google.maps.Point(-10, -2))
        }}
      />
    )
  }

  render() {

    var facilityFenceCoords = []
    fence['features'][0]['geometry']['coordinates'].reduce((prev, curr, index, arr) => {
      var corrd = { lat: curr[1], lng: curr[0] }
      facilityFenceCoords.push(corrd)
    }, {})

    var featureCollectionsCoords0 = []
    featureCollectionCoords['features'][0]['geometry']['coordinates'][0].reduce((prev, curr, index, arr) => {
      var corrd = { lat: curr[1], lng: curr[0] }
      featureCollectionsCoords0.push(corrd)
    }, {})

    var featureCollectionsCoords1 = []
    featureCollectionCoords['features'][1]['geometry']['coordinates'][0].reduce((prev, curr, index, arr) => {
      var corrd = { lat: curr[1], lng: curr[0] }
      featureCollectionsCoords1.push(corrd)
    }, {})

    var featureCollectionsCoords2 = []
    featureCollectionCoords['features'][2]['geometry']['coordinates'][0].reduce((prev, curr, index, arr) => {
      var corrd = { lat: curr[1], lng: curr[0] }
      featureCollectionsCoords2.push(corrd)
    }, {})

    const { google } = this.props

    let downMarks = []
    downStations['features'].reduce((prev, curr, index, arr) => {
      var corrd = { lat: curr['geometry']['coordinates'][1], lng: curr['geometry']['coordinates'][0] }
      var name = curr['properties']['STATION_ID']
      downMarks.push(this.makeMarker(name, corrd, '#2a2d2b', '#75a1ea'))
    }, {})

    let upMarks = []
    upStations['features'].reduce((prev, curr, index, arr) => {
      var corrd = { lat: curr['geometry']['coordinates'][1], lng: curr['geometry']['coordinates'][0] }
      var name = curr['properties']['STATION_ID']
      upMarks.push(this.makeMarker(name, corrd, '#2a2d2b', '#00e531'))
    }, {})

    return (
      <Map google={google}  mapType={'satellite'}
        zoom={14}
        onClick={this.onMapClicked}
        initialCenter={this.state.center}
        style={{ width: '100%', height: '100%', position: 'relative' }}
        className={'map'}
        onReady={this.onMapReady} >
        {downMarks}{upMarks}

        <Polygon
          paths={facilityFenceCoords}
          strokeColor="#fcbb64"
          strokeOpacity={1.0}
          strokeWeight={2}
          fillColor="#f7f3e1"
          fillOpacity={0.1} />

        <Polygon
          paths={featureCollectionsCoords0}
          strokeColor="#fcbb64"
          strokeOpacity={1.0}
          strokeWeight={0}
          fillColor="#b7bc25"
          fillOpacity={0.9} />

        <Polygon
          paths={featureCollectionsCoords1}
          strokeColor="#fcbb64"
          strokeOpacity={1.0}
          strokeWeight={0}
          fillColor="#f4ab2c"
          fillOpacity={0.9} />

        <Polygon
          paths={featureCollectionsCoords2}
          strokeColor="#fcbb64"
          strokeOpacity={1.0}
          strokeWeight={0}
          fillColor="#b7bc25"
          fillOpacity={0.9} />
      </Map>
    )
  }
}

export default GoogleApiWrapper({
  apiKey: ('AIzaSyCkVtTOEli1pN0_OE3N8jMn-p6RccuWi7I')
})(SimpleMap)



